:root {
  --background-primary: black;
  --foreground-primary: white;
  --link-text-primary: white;
  --link-text-active-primary: red;
  --card-background: #20100a;
  --card-foreground: var(--foreground-primary);
  --background-secondary: darkgray;
  --foreground-secondary: black;
  --link-text-secondary: black;
  --link-text-active-secondary: #820000;
  --background-accent: red;
  --foreground-accent: white;
  --toggle-selected: #908858;
  --spacing-medium: 12px;
  --spacing-tiny: 8px;
  --spacing-xtiny: 4px;
}

body {
  height: 100vh;
  background-color: var(--background-primary);
  color: var(--foreground-primary);
  flex-direction: column;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
}

main {
  flex-grow: 1;
}

button {
  all: unset;
  background-color: var(--background-secondary);
  padding: var(--spacing-tiny);
  color: var(--foreground-secondary);
  border-radius: 4px;
}

button.active {
  background-color: var(--link-text-active-secondary);
  color: #908858;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h4 {
  text-align: center;
  margin-bottom: 0;
}

header a {
  color: var(--link-text-active-secondary);
}

a {
  color: var(--link-text-active-primary);
}

#references {
  text-align: center;
}

.runeword-actions {
  gap: var(--spacing-xtiny);
  margin: var(--spacing-medium);
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.header-block h3, .header-block h4 {
  margin-top: 0;
  margin-bottom: 0;
}

.header-block h3 {
  color: #908858;
}

.header-block {
  background-color: var(--card-background);
  color: var(--card-foreground);
  padding: var(--spacing-tiny);
  cursor: pointer;
  border: 1px solid var(--link-text-active-secondary);
}

.flex-apart {
  justify-content: space-between;
  display: flex;
}

#runewords > ul, #runes > ul {
  max-width: 650px;
  margin-top: 1.33em;
  margin-left: auto;
  margin-right: auto;
}

#runewords > ul > li {
  margin-bottom: var(--spacing-xtiny);
}

#runewords > div:nth-child(4) > div {
  margin-top: 2em;
}

.runeword-effects {
  text-align: center;
  border: 1px solid var(--background-secondary);
  padding: var(--spacing-xtiny);
  color: #4850a8;
  border-top: none;
}

header {
  margin-bottom: var(--spacing-medium);
}

footer, header {
  text-align: center;
  background-color: var(--background-secondary);
  color: var(--foreground-secondary);
}

footer {
  padding: var(--spacing-medium);
  margin-top: var(--spacing-medium);
}

header nav ul {
  gap: var(--spacing-medium);
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

header nav ul li {
  padding: var(--spacing-medium) 0px var(--spacing-medium) var(--spacing-medium);
}

header nav ul li a {
  color: var(--link-text-secondary);
}

header nav ul li a.nav-active {
  color: var(--link-text-active-secondary);
}

section {
  padding-left: var(--spacing-medium);
  padding-right: var(--spacing-medium);
}

.switch {
  width: 60px;
  height: 24px;
  align-items: center;
  display: flex;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  height: 100%;
  width: 100%;
  background-color: gray;
  transition: all .4s;
  position: absolute;
}

.slider:before {
  content: "";
  height: 16px;
  width: 26px;
  background-color: #fff;
  transition: all .4s;
  position: absolute;
  bottom: 4px;
  left: 4px;
}

input:checked + .slider {
  background-color: var(--toggle-selected);
}

input:focus + .slider {
  box-shadow: 0 0 8px #2196f3;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

.switch-text {
  white-space: nowrap;
  left: 68px;
}

.switch-container {
  flex-direction: column;
  align-items: center;
  display: flex;
}

